import React, { useContext } from 'react';
import './Header.css';
import {Link} from 'react-router-dom'
import { userContext } from '../../App';
import { googleSignIn } from '../Login/LoginManager';

const Header = () => {
    const [logedIn,setLogedInUser] = useContext(userContext)
    const handleSing =()=>{
        googleSignIn()
        .then(res=>setLogedInUser(res))
    }
    return (
        <div style={{backgroundColor:'black', height:'80px', color:'white', textAlign:'center'}}>
            <h2>AMAZON</h2>
            <div>
                <nav>
                    <Link to='/shop'>Shop</Link>
                    <Link to='/review'>Review</Link>
                    <Link to='/inventory'>Inventory</Link>
                    <button onClick={()=>{setLogedInUser({})}}>SingOut</button> 
                   
                   
                </nav>
            </div>
        </div>
    );
};

export default Header;