import Header from './Component/Header/Header'
import Shop from './Component/Shop/Shop';
import {
  BrowserRouter,
  Switch,
  Route
} from "react-router-dom";
import SingleProduct from './Component/SingleProduct/SingleProduct';
import Review from './Component/Review/Review';
import Shipment from './Component/Shipment/Shipment';
import Login from './Component/Login/Login';
import { createContext, useState } from 'react';
import PrivateRoutes from './Component/PrivateRoute/PrivateRoutes';
import Inventory from './Component/Inventory/Inventory';
export const userContext = createContext()





function App() {
  const [logedIn,setLogedInUser]= useState({})
  
  return (
    <userContext.Provider value={[logedIn,setLogedInUser]}>
      <BrowserRouter>
      <h2>Hello {logedIn.email}</h2>
      <Header></Header>
      <Switch>
        <Route path='/' exact>
          <Shop></Shop>
        </Route>
        <Route path='/shop'>
          <Shop></Shop>
        </Route>
        <Route path='/product/:id'>
          <SingleProduct></SingleProduct>
        </Route>
        <Route path='/review'>
        <Review></Review>
        </Route>
        <PrivateRoutes path='/shipment'>
            <Shipment></Shipment>
        </PrivateRoutes>

        <PrivateRoutes path='/inventory'>
          <Inventory></Inventory>
        </PrivateRoutes>
        <Route path='/login'>
        <Login></Login>
        </Route>
      </Switch>
      </BrowserRouter>
    </userContext.Provider>
  );
}

export default App;
