import React from 'react';

const ReviewItem = (props) => {
    const product = props.product
    return (
        <div>
            <h2>{product.name}</h2>
            <h3>{product.quantity}</h3>
            <h4>{product.price}</h4>
            <button onClick={()=>{props.removeItem(product.id)}}>Remove Item</button>
        </div>
    );
};

export default ReviewItem;