import React, { useEffect, useState } from 'react';
import products from '../fakeData/products.json'
import Product from '../Product/Product';
import Cart from '../Cart/Cart';
import { Link } from 'react-router-dom';
import { addToDb, getShoppingCart } from '../utilities/fakedb';

const Shop = () => {
    const [product,setProduct]= useState([])
    useEffect(()=>{
        setProduct(products)
    },[])

    const [cart,setCart]=useState([])
    useEffect(()=>{
        const saveData = getShoppingCart()
        const pId = Object.keys(saveData)
        const prevCart = pId.map(id=>{
            const product = products.find(item=>item.id === id)
            product.quantity = saveData[id]
            return product
        })
        setCart(prevCart)
    },[])
    const addCart =(product)=>{
            const sameProduct = cart.find(item=> item.id === product.id)
            let count= 1;
            let newCart;
            if(sameProduct){
                count = sameProduct.quantity + 1;
                sameProduct.quantity = count;
                const others = cart.filter(item=> item.id !== product.id)
                newCart = [...others,sameProduct]
            }
            else{
                product.quantity = 1;
                newCart = [...cart,product]
            }
            setCart(newCart)
            addToDb(product.id,count)
    }
    return (
        <div style={{display:'flex',justifyContent:'space-around'}}>
            <div>
                {
                    product.map((x,y)=><Product product={x} key={y} addCart={addCart}></Product>)
                }
            </div>

            <div>
                <Cart cart={cart}>
                    <Link to={'/review'}><button>Review Order</button></Link>
                </Cart>
            </div>
        </div>
    );
};

export default Shop;