import React, { useContext, useState } from 'react';
import { getAuth, signInWithPopup, GoogleAuthProvider, signOut, createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";
import { userContext } from '../../App';
import { useHistory, useLocation } from 'react-router-dom';
import { createUser, googleSignIn,initializeAppWork, signInUser, signOutUser,  } from './LoginManager';


// initializeApp(firebaseConfig);

const Login = () => {
    
    const [newUser, setNewUser] = useState(false);
    const [user, setUser] = useState({
        isSigned: false,
        name: '',
        email: '',
        password: '',
        success: false
    });

    initializeAppWork()

    const [logedIn,setLogedInUser]= useContext(userContext)
    
    const history = useHistory()
    const location = useLocation()
    let { from } = location.state || { from: { pathname: "/" } };
    
    
    const handleSignIn = async() => {
    googleSignIn()
    .then(res=>{
        ress(res,true)
    })
  }

    const handleSignOut = () => {
        signOutUser()
        .then(res=>{
            ress(res,false)
        })
    }

    const handleBlur = (e) => {
        let formValid = true;
        if (e.target.name === 'email') {
            formValid = /^\S+@\S+\.\S+$/.test(e.target.value);
        }
        if (formValid) {
            const newUserInfo = { ...user };
            newUserInfo[e.target.name] = e.target.value;
            setUser(newUserInfo);
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if (newUser && user.email && user.password) {
            createUser(user.email, user.password).then(res=>{
                ress(res,true)
            })
        }
        if (!newUser && user.email && user.password) {
            signInUser(user.email,user.password).then(res=>{
                ress(res,true)
            })
        }
    }

    const ress =(res,redirect)=>{
        setUser(res)
        setLogedInUser(res)
        if(redirect){
            history.replace(from)
        }
        
    }
    return (
        <div>
            {user.isSigned ? <button onClick={handleSignOut}>Sign Out</button> :
                <button onClick={handleSignIn}>Sign In</button>}
            {
                user.isSigned && <p>Welcome {user.email}</p>
            }

            <h2>name:{user.name}</h2>
            <h2>email:{user.email}</h2>
            New User:<input type='checkbox' onChange={() => setNewUser(!newUser)}></input>
            
                <form onSubmit={handleSubmit}>
                    {newUser && <input type='text' name='name' placeholder='enter your name' onBlur={handleBlur} />}
                    Email: <input type='email' name='email' placeholder='enter your email' onBlur={handleBlur} />
                    Password: <input type='password' name='password' placeholder='enter your password' onBlur={handleBlur} />
                    {newUser ? <button type='submit'>Submit</button>:<button type='submit'>Login</button>}
                </form>
            {
                <p style={{ color: 'red' }}>{user.error}</p>
            }
            {
                user.success && <p style={{ color: 'green' }}>{newUser ? 'created':'loged in'}Successfully</p>
            }
        </div>
    );
};

export default Login;


