import React, { useEffect, useState } from 'react';
import { deleteShoppingCart, getShoppingCart, removeFromDb } from '../utilities/fakedb';
import fakeData from '../fakeData/products.json'
import ReviewItem from '../ReviewItem/ReviewItem';
import Cart from '../Cart/Cart';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';


const Review = () => {
    const [product,setProduct]= useState([])
    useEffect(()=>{
        const saveData = getShoppingCart()
        const pId = Object.keys(saveData)
        const products = pId.map(id=>{
            const product = fakeData.find(item=> item.id === id)
            product.quantity = saveData[id]
            return product;
        })
        setProduct(products)
    },[])

    const [cart,setCart]= useState([])
    useEffect(()=>{
        setCart(product)
    },[product])

    const removeItem =(id)=>{
            const newCart = cart.filter(item=> item.id !== id)
            setProduct(newCart)
            removeFromDb(id)
    }
    const [order,setOrder]=useState(false)
    const history = useHistory()

    const proccedCheckOut =()=>{
        history.push(`/shipment`)
    }

    let thankyou;
    if(order){
        thankyou = 'Thanks for your Order'
    }
    return (
        <div style={{display:'flex',justifyContent:'space-around'}}>
            <div>
                {
                    product.map((x,y)=><ReviewItem product={x} key={y} removeItem={removeItem}></ReviewItem>)
                }
                {thankyou}
                
            </div>

            <div>
                    <Cart cart={cart}>
                        <button onClick={()=>{proccedCheckOut()}}>Procced Checkout</button>
                    </Cart>
            </div>
        </div>
    );
};

export default Review;

