import React from 'react';

const Cart = (props) => {
    const cart = props.cart
    const total = cart.reduce((x,y)=> x + y.price*y.quantity,0)
    return (
        <div>
            <h2>total cart {cart.length}</h2>
            <h3>total {total}</h3>
            {
                props.children
            }
        </div>
    );
};

export default Cart;